@import "variables";

@font-face {
	font-family: $pipFont;
	font-style: normal;
	font-weight: 400;
	src: local('Share Tech Mono'), local('ShareTechMono-Regular'), url(/fonts/ShareTechMono.woff2) format('woff2');
	unicode-range: U+0000-00FF, U+0131, U+0152-0153, U+02C6, U+02DA, U+02DC, U+2000-206F, U+2074, U+20AC, U+2212, U+2215, U+E0FF, U+EFFD, U+F000;
}


/* devanagari */
@font-face {
	font-family: $pipFontNormal;
	font-style: normal;
	font-weight: 400;
	src: local($pipFontNormal), local('PragatiNarrow-Regular'), url(https://fonts.gstatic.com/s/pragatinarrow/v2/HzG2TfC862qPNsZsV_djPlibR2f8S0J2B_EaDttRRhw.woff2) format('woff2');
	unicode-range: U+02BC, U+0900-097F, U+1CD0-1CF6, U+1CF8-1CF9, U+200B-200D, U+20A8, U+20B9, U+25CC, U+A830-A839, U+A8E0-A8FB;
}
